import Loader from 'components/Loader'
import Tasks from 'components/views/Tasks'
import { AnimatePresence } from 'framer-motion'
import { lazy, Suspense, useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { Route, Routes, useLocation } from 'react-router-dom'
import { DONATE, HOME, LOGIN, REGISTER, REGISTERED, TEAM_ID } from 'routes'
import { getTasks } from 'store/actions/tasksActions'
import { demoModeOnly } from 'utils/modeDemoOnly'

const Login = lazy(() => import('components/views/auth/Login'))
const Register = lazy(() => import('components/views/auth/Register'))
const Registered = lazy(() => import('components/views/Registered'))
const Error404 = lazy(() => import('components/views/Erro404'))
const Donate = lazy(() => import('components/views/Donate'))

export const App = () => {
  const location = useLocation()
  const dispatch = useDispatch()

  useEffect(() => {
    ;(async () => {
      await demoModeOnly()
      dispatch(getTasks())
    })()
  }, [])

  return (
    <AnimatePresence exitBeforeEnter>
      <Suspense fallback={<Loader />}>
        <Routes location={location} key={location.pathname}>
          <Route path={LOGIN} element={<Login />} />
          <Route path={REGISTER} element={<Register />} />
          <Route path={REGISTERED + TEAM_ID} element={<Registered />} />
          <Route path={HOME} element={<Tasks />} />
          <Route path={DONATE} element={<Donate />} />
          <Route path='*' element={<Error404 />} />
        </Routes>
      </Suspense>
    </AnimatePresence>
  )
}
