import { postAuth } from 'service/auth'
import swal from './swal'

export const demoModeOnly = async () => {
  try {
    const res = await postAuth('login', { userName: 'userLeader', password: 'Pass123' })
    if (res.status_code !== 200) return swal()
    window.localStorage.setItem('token', res.result?.token)
    window.localStorage.setItem('userName', res.result?.user.userName)
  } catch (error) {
    return swal('error')
  }
}
